.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.button {
  padding: 6px 10px;
  background-color: #84c1ff;
  border: 0;
  color: white;
  font-size: 14px;
  outline: none;
  cursor: pointer;
}

.button-red {
  padding: 6px 10px;
  background-color: #ee4035;
  border: 0;
  color: white;
  font-size: 14px;
  outline: none;
  cursor: pointer;
}

.button-disabled {
  background-color: #ccc;
  cursor: not-allowed;
}


